      }
    },
    mounted() {
       this.administrationList()
    },
    methods: {
      handleSelect(key) {
        this.activeIndex = key
        console.log(key)
        console.log(typeof key)
      },
      administrationList() {
        this.departmentList()
        this.programList()
        let URL = '/checkin/users'
        this.$ajax.get(URL)
          .then(response => {
            this.personnelList = response.data
            console.log(this.personnelList)
          })
      },
      departmentList() {
        let URL = '/departments'
        this.$ajax.get(URL)
          .then(response => {
            this.departmentlList = response.data
            console.log(this.departmentlList)
          })
      },
      consent() {
        console.log('同意此用户加入年会')
        let URL = '/departments'
        // this.$ajax.get(URL)
        //   .then(response => {
        //     this.personnelList = response.data
        //     console.log(this.personnelList)
        //   })
      },
      refuse() {
        console.log('拒绝此用户加入年会')
        let URL = '/departments'
        // this.$ajax.get(URL)
        //   .then(response => {
        //     this.personnelList = response.data
        //     console.log(this.personnelList)
        //   })
      },
      addDepartment() {
        console.log('添加部门')
        let postData = Qs.stringify({
          name: '人事部'
        })
        this.$ajax.post('/departments/', postData,{
            headers: {
              'Content-Type': 'application/x-www-form-urlencoded'
            }
        })
          .then(response => {
            console.log(response)
            this.departmentList()
          })
      },
      delDepartment(item) {
        console.log('删除部门')
        this.$ajax.delete('/departments/' + item.departmentId)
          .then(response => {
            console.log(response)
            this.departmentList()
          })
      },
      addProgram() {
          this.programCard = true
        console.log('添加节目')
      },
      delProgram() {
        console.log('删除节目')
      },
      alterProgram() {
        console.log('修改节目')
      },
      programList () {
        let URL = '/programmes'
        this.$ajax.get(URL)
          .then(response => {
            this.programTotalList = response.data
            console.log(this.programTotalList)
          })
      },
      TotalListItem () {
//        console.log(item)
//        console.log(index)
        console.log(this.checkedNames)

      }
    }
