  <template>
    <div class="main">
      <div class="content">
        <div class="personal">
          <p class="img"><img src="../assets/images/04.jpg" alt=""></p>
          <p class="personal-name">邓小一</p>
          <p class="personal-seat">
            <span>年会桌数为</span>
            <span class="mark">12号</span>
          </p>
        </div>
        <router-link to='/Interactive'>
          <div class="interactive">
          </div>
        </router-link>
        <router-link to='/Process'>
          <div class="process">
          </div>
        </router-link>
      </div>
    </div>
  </template>

  <script>
    export default {
      name: 'Sign',
      data() {
        return {}
      }
    }
  </script>



  <style lang="scss" scoped>
    @import '../assets/scss/layout.scss';
    .main {
      background: url("../assets/images/sign-bg.jpg") no-repeat center / 100% #df343c;
      .content {
        height: 55%;
        margin-top: 90%;
        color: #dab866;
        text-align: center;
        .personal {
          margin-bottom:  100px;
          .img {
            width: 30%;
            height: 250px;
            margin: 0px auto 10px;
            text-align: center;
            img {
              width: 58%;
              margin: 21% 5px 0 0;
              border-radius: 50%;
            }
            background: url("../assets/images/personal-bg.png") no-repeat center / 100%;
          }
          p {
            margin-bottom: 20px;
            text-align: center;
          }
          .personal-name {
            font-size: 30px;
          }
          .personal-seat {
            font-size: 40px;
            font-weight: 500;
            .mark {
              color: #dab866;
            }
          }
        }
        .interactive {
          width: 42%;
          height: 110px;
          display: inline-block;
          background: url("../assets/images/chat-interaction.png") no-repeat center / 80%;
        }
        .process {
          width: 42%;
          height: 110px;
          display: inline-block;
          background: url("../assets/images/program-rundown.png") no-repeat center / 80%;
        }
      }
    }
  </style>




